import React from 'react';
import { Tabs } from 'antd';
import { withRouter } from 'react-router-dom';
import styles from './index.module.less';

/**
 * 
 * @param { props } activeTab string;
 */

function SystemTabs(props){
    const menuList = JSON.parse(window.localStorage.getItem("leftMenuList")).menus;
    const systemMenu = menuList.find(item => item.url==="/systemSettings") || {};
    const systemTabs = systemMenu.subMenus || [];
    const callback = (key) => {
        props.history.push(`/systemSettings/${key}`)
    }
    return (
        <Tabs activeKey={props.activeTab} onChange={callback} className={styles.tabs}>
            {
                systemTabs.map(item => (<Tabs.TabPane tab={item.name} key={item.url.replace("/systemSettings/","")}></Tabs.TabPane>))
            }
            {/* <Tabs.TabPane tab="流程管理" key="workFlowManagement"></Tabs.TabPane>
            <Tabs.TabPane tab="用户管理" key="userManagement"> </Tabs.TabPane>
            <Tabs.TabPane tab="菜单管理" key="menuManagement"> </Tabs.TabPane>
            <Tabs.TabPane tab="部门管理" key="departmentManagement"> </Tabs.TabPane>
            <Tabs.TabPane tab="汇总表模板管理" key="summaryTemplateManagement"> </Tabs.TabPane>
            <Tabs.TabPane tab="角色管理" key="roleManagement"> </Tabs.TabPane> */}
        </Tabs>
    )
}

export default withRouter(SystemTabs)