import React, { Component } from 'react';
import { Button, Icon, Modal, Row, Col, Input, message, Popconfirm} from 'antd';
import styles from './index.module.less'
import {postAction} from '@/axios'
import {hasButtonPermission} from '@/util'

const MyIcon = Icon.createFromIconfontCN({
    scriptUrl:"//at.alicdn.com/t/font_1331187_no4dbj3r2x.js"
})

export default class UserContainer extends Component {
    constructor(props){
        super(props);
        this.state = {
            userInfo:{},
            userList:[],
            showModal:false,
            isAdd:true,//新增/修改,
            inputValue:""
        }
    }
    componentDidMount() {
        this.getData()
    }
    cancelChild=()=>{
        this.setState({
            userInfo:{}
        })
    }
    getData=()=>{
        postAction(
            `/ybt/system/setting/role/getRoleList`,
            {}
        ).then(res=>{
            if(res.success){
                // 过滤掉超级管理员
                let userList=res.obj
                // .filter(item=>item.roleName !== '超级管理员')
                // let userList=res.obj
                this.setState({
                    userList
                })
            }
        })
    }
    //点击新增按钮
    handleAddModal = () => {
        this.setState({isAdd:true,showModal:true,inputValue:""})
    }
    //点击编辑按钮
    handleEditModal = (item,e) => {
        
        e.nativeEvent.stopImmediatePropagation();
        e.stopPropagation();
        this.setState({isAdd:false,showModal:true,inputValue:item.roleName,userInfo:item})
    }
    handleCancle=()=>{
        this.setState({
            showModal:false,
            inputValue:''
        })
    }
    //确定按钮
    handleOk = () => {
        if(this.state.inputValue){
            if(this.state.isAdd){
                postAction(
                    `/ybt/system/setting/role/createRole`,
                    {
                        roleName:this.state.inputValue
                    }
                ).then(res=>{
                    if(res.success){
                       message.success('操作成功')
                       this.getData()
                    }else{
                        message.error(res.obj)
                    }
                })
                this.setState({showModal:false,inputValue:""})
            }else{
                postAction(
                    `/ybt/system/setting/role/updateRole`,
                    {
                        roleName:this.state.inputValue,
                        roleId:this.state.userInfo.roleId
                    }
                ).then(res=>{
                    if(res.success){
                       message.success('操作成功')
                       this.getData()
                    }else{
                        message.error(res.obj)
                    }
                })
                this.setState({showModal:false,inputValue:""})
            }
        }else{
            message.warn("角色名不能为空！")
        }
    }
    //input框改变
    handleChange = (e) => {
        this.setState({inputValue:e.target.value})
    }
    //点击行
    clickItem = (userInfo) => {
        this.setState({userInfo});
        this.props.updateUserInfo(userInfo);
    }
    //删除
    handDelete = (item) => {
        postAction(
            `/ybt/system/setting/role/deleteRole`,
            [item.roleId]
        ).then(res=>{
            if(res.success){
               message.success('操作成功')               
               this.props.updateUserInfo({})
               this.getData()
            }else{
                message.error(res.obj)
            }
        })
    }
    render() {
        return (
            <div className={styles.userContainer}>
                <div className={styles.addUser}>
                    {
                        hasButtonPermission('role001') && 
                        <Button icon="plus" type="primary" onClick={this.handleAddModal} size="small">新增角色</Button>

                    }
                </div>
                <div className={styles.userList}>
                    {
                        this.state.userList.map(item => {
                            return (
                                <div 
                                    className={this.state.userInfo.roleId===item.roleId?styles.current:styles.item} 
                                    key={item.roleId} 
                                    onClick={()=>{this.clickItem(item)}}
                                >
                                    {item.roleName}
                                    {
                                        this.state.userInfo.roleId===item.roleId && 
                                        <div className={styles.icon}>
                                            {
                                                hasButtonPermission('role002') &&
                                                <MyIcon type="icon-ai61" onClick={this.handleEditModal.bind(this,item)}/>

                                            }
                                            {
                                                hasButtonPermission('role003') && 
                                            <Popconfirm
                                                title="确定删除该角色吗？"
                                                onConfirm={()=>{this.handDelete(item)}}
                                                okText="确定"
                                                cancelText="取消"
                                            >
                                                <MyIcon type="icon-shanchu"/>
                                            </Popconfirm>
                                            }
                                            
                                        </div>
                                    }
                                </div>
                            )
                        })
                    }
                </div>
                <Modal
                    visible={this.state.showModal}
                    title={this.state.isAdd?"新增角色":"修改"}
                    footer={false}
                    width={500}
                    onCancel={this.handleCancle}
                >
                    <Row>
                        <Col span={5} style={{lineHeight:"32px"}}>角色名称：</Col>
                        <Col span={19}>
                            <Input placeholder="请输入角色名称" value={this.state.inputValue} onChange={this.handleChange}/>
                        </Col>
                    </Row>
                    <div style={{textAlign:"right",marginTop:"20px"}}>
                        <Button type="primary" style={{marginRight:"16px"}} onClick={this.handleOk}>确认</Button>
                        <Button onClick={this.handleCancle}>取消</Button>
                    </div>
                </Modal>
            </div>
        )
    }
}
