import React, { Component } from 'react';
import { Button, Tree,message } from 'antd';
import styles from './index.module.less';
import {postAction} from '@/axios'
import {hasButtonPermission} from '@/util'
const TreeNode = Tree.TreeNode;

export default class PowerManager extends Component {
    constructor(props){
        super(props);
        this.state = {
            allSelectPermissionIds:[],//选择的权限
            halfSelectPermissionIds:[],//半选状态
            data:[]
        }
    }
    onCheck = (checkedKeys,e) => {
        const halfCheckedKeys = e.halfCheckedKeys;
        this.setState({ allSelectPermissionIds:checkedKeys,halfSelectPermissionIds:halfCheckedKeys });
    };
    componentDidUpdate(preProps){
        if(JSON.stringify(preProps) !== JSON.stringify(this.props) && this.props.userInfo.roleId){
            this.getPermission(this.props)
        }
    }
    renderTree = (data) => data.map(item => {
        if (item.children) {
            return (
              <TreeNode title={item.name} key={item.permissionId}>
                {this.renderTree(item.children)}
              </TreeNode>
            );
        }
        return <TreeNode {...item} />;
    });
    handleOk = () => {
        // console.log("选择的权限：",this.state.checkedKeys);
        // console.log("当前角色信息：",this.props.userInfo);
        let param={
            "allSelectPermissionIds": this.state.allSelectPermissionIds,
            "halfSelectPermissionIds": this.state.halfSelectPermissionIds,
            "roleId": this.props.userInfo.roleId
        }
        //发送请求
        postAction(
            `/ybt/system/setting/role/createRolePermission`,
            param
        ).then(res=>{
            if(res.success){
                message.success('操作成功')
                this.getData()
            }else{
                 message.error(res.obj)
            }
        })
    }
    componentDidMount() {
        this.getData()
    }
    getData=()=>{        
        postAction(
            `/ybt/system/setting/permission/getPermissionList?pageNum=1&pageSize=999`,
            {
                pageNum:1,
                pageSize:999
            }
        ).then(res=>{
            if(res.success){
                this.setState({
                    data:res.obj.rows
                })
            }
        })        
    }
    getPermission=(props)=>{
        postAction(
            `/ybt/system/setting/role/getPermissionList`,
            {
                roleId:props.userInfo.roleId
            }
        ).then(res=>{
            if(res.success){
                this.setState({
                    allSelectPermissionIds:res.obj.allSelectPermissionIds,
                    halfSelectPermissionIds:res.obj.halfSelectPermissionIds,                    
                })
            }
        })  
    }
    handleCancel=()=>{
        this.props.handleCancel()
    }
    render() {
        return (
            <div className={styles.powerManager}>
                <div className={styles.title}>角色权限管理</div>
                <div className={styles.treeList}>
                    {
                        this.props.userInfo.roleId?<Tree
                        checkable
                        onCheck={this.onCheck}
                        checkedKeys={this.state.allSelectPermissionIds}
                    >
                        {this.renderTree(this.state.data)}
                    </Tree>:null
                    }
                
                </div>
                {
                    this.props.userInfo.roleId?<div className={styles.btnContainer}>
                        {
                            hasButtonPermission('role004') && 
                            <Button type="primary" onClick={this.handleOk}>确认</Button>
                        }
                        <Button onClick={this.handleCancel}>取消</Button>
                    </div>:null
                }
                
            </div>
        )
    }
}
