import React, { Component } from 'react';
import { Card } from 'antd';
import UserContainer from './userContainer';
import PowerManager from './powerManager';
import styles from './index.module.less';
import SystemTabs from '../common/systemTabs';
export default class roleManagement extends Component {
    constructor(props){
        super(props);
        this.state = {
            userInfo:{}
        }
    }
    updateUserInfo = (userInfo) => {
        this.setState({userInfo})
    }
    handleCancel=()=>{
        this.setState({
            userInfo:{}
        },()=>{
            this.child.cancelChild()
        })
    }
    render() {
        return (
            <React.Fragment>
                <SystemTabs activeTab="roleManagement"/>
                <div style={{padding:20}}>
                    <Card title={false} bodyStyle={{padding:"0px"}}>
                        <div className={styles.container}>
                            <UserContainer ref={(child) => { this.child = child }} updateUserInfo = {(userInfo) => {this.updateUserInfo(userInfo)}}/>
                            <div className={styles.splitLine}></div>
                            <PowerManager userInfo={this.state.userInfo} handleCancel={this.handleCancel}/>
                        </div>
                    </Card>
                </div>
            </React.Fragment>
            
        )
    }
}
